import React from "react";
import styled, { keyframes } from "styled-components";
import PropTypes from "prop-types";

export const TOAST_TYPE = {
  SUCCESS: "success",
  WARNING: "warning",
  CAUTION: "caution"
}

const ToastAnimate = keyframes`
  0% {
    opacity: 0.5;
    transform: translateY(13.3rem);
  }
  25% {
    opacity: 0.8;
    transform: translateY(-20rem);
  }
  50% {
    opacity: 1;
    transform: translateY(-20rem);
  }
  75% {
    opacity: 1;
    transform: translateY(-20rem);
  }
`;

const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  position: fixed;
  width: 100%;
  height: 100%;
  left:0;
`;

const ToastStyles = styled.div`
  font-family: var(--font-urw-din);
  font-size: 1rem;
  line-height: 1.5em;
  min-height: 4rem;
  display: flex;
  flex-direction: row;
  position: fixed;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 0 1rem;
  bottom: -13.3rem;
  span {
    margin-left: 1.83rem;
    padding: 1.125rem 0;
    dispaly: table-cell;
    vertical-align: center;
    max-width: 80ch;
  }
  p {
    margin: 0 0 1.5rem 0 !important;
    max-width: 80ch !important;

    &:last-of-type {
      margin-bottom: 0 !important;
    }
  }
  &:before {
    display: inline-block;
    width: 1.3rem;
    height: 1.3rem;
    position: absolute;
    top: 2rem;
    transform: translateY(-50%);
  }
  animation: ${ToastAnimate} 5s;
  ${({ type }) => {
    if (type === TOAST_TYPE.CAUTION) {
      return `
        background: var(--background-yellow);
        border: 1px solid var(--medium-yellow);
        color: var(--caution-yellow);
        &:before {
          content: url(//images.arcteryx.com/foundation-ui/svgs/Caution_Icon.svg);
        }
        p, a {
          color: var(--caution-yellow) !important;
        }
      `;
    }
    if (type === TOAST_TYPE.WARNING) {
      return `
        background: var(--background-red);
        border: 1px solid var(--medium-red);
        color: var(--warning-red);
        &:before {
          content: url(//images.arcteryx.com/foundation-ui/svgs/Warning_Icon.svg);
        }
        p, a {
          color: var(--warning-red) !important;
        }
      `;
    }
    if (type === TOAST_TYPE.SUCCESS) {
      return `
        background: var(--background-green);
        border: 1px solid var(--medium-green);
        color: var(--success-green);
        &:before {
          content: url(//images.arcteryx.com/foundation-ui/svgs/Success_Icon.svg);
        }
        p, a {
          color: var(--success-green) !important;
        }
      `;
    }
  }}
`;
const Toast = ({ type, dangerouslySetInnerHTML, children, className }) => {
  if (type) {
    return (
      <Wrapper>
        <ToastStyles className={className} type={type}>
          {dangerouslySetInnerHTML ? (
            <span dangerouslySetInnerHTML={{ __html: children }}></span>
          ) : (
            <span>{children}</span>
          )}
        </ToastStyles>
      </Wrapper>
    );
  }
  return null;
};

Toast.defaultProps = {
  type: TOAST_TYPE.CAUTION,
};

Toast.prototype = {
  type: PropTypes.string.isRequired,
};

export default Toast;
